import React from 'react';
import NewsCard from '../NewsCard/NewsCard';
import { Grid, Grow, Typography } from '@material-ui/core';
// import styles from './styles';
import useStyles from './styles.js';

const infoCards = [
    { color: '#00838f', title: 'This is an ai powered news app. You can also have a little convo. with our beloved alan', text: 'categories, terms or sources that you want the news from' },
    { color: '#1565c0', title: 'News by Categories', info: 'Business, Entertainment, General, Health, Science, Sports, Technology', text: 'Give me the latest Technology news' },
    { color: '#4527a0', title: 'News by terms', info: 'Bitcoin, PlayStation 5, Smartphones, Elon Musk..', text: 'What\'s up with PlayStation 5' },
    { color: '#283593', title: 'News by Sources', info: 'Wired, BBC News, Time, ABC News, Google News, TechCrunch, Engadget..', text: 'Show me the news from ABC News' }
]

const NewsCards = ({ articles, activeArticle }) => {

    const classes = useStyles();

    if (!articles.length) {
        return (
            <Grow in>
                <Grid className={classes.container} container alignItems='stretch' spacing={3}>
                    {infoCards.map((infoCard, i) => (
                        <Grid key={i} item xs={12} sm={6} md={4} lg={3} className={classes.infoCard}>
                            <div className={classes.card} style={{ backgroundColor: infoCard.color }}>
                                <Typography variant='h5'>{infoCard.title}</Typography>
                                {
                                    infoCard.info
                                        ? (<Typography variant='h6' className={classes.font}>
                                            <strong>
                                                {infoCard.title.split(' ')[2]}:
                                                <br />
                                            </strong>
                                            {infoCard.info}
                                        </Typography>) : null
                                }
                                <Typography className={classes.font} variant='h6'>Try Saying: <br /> <i>{infoCard.text}</i></Typography>
                            </div>
                        </Grid>
                    ))}
                </Grid>
            </Grow >
        );
    }

    return (
        <Grow in>
            <Grid className={classes.container} container alignItems='stretch' spacing={3}>
                {articles.map((article, i) => (
                    <Grid key={i} item xs={12} sm={6} md={4} lg={3} style={{ display: 'flex' }}>
                        <NewsCard article={article} activeArticle={activeArticle} i={i} />
                    </Grid>
                ))}
            </Grid>
        </Grow >
    )
}

export default NewsCards;